<template>
    <div>
        <p>1WordJSON</p>
        <textarea v-model="data"></textarea>
        <p>2AjaxWord public/static/template.docx</p>
        <p>3 JSON + Word Word
            <button @click="generateDoc"></button>
            
        </p>
    </div>
</template>

<script>
  const json = require("../config/template.json");
  // const nzhcn = require("nzh/cn");
  const saveDataToFile = (data, fileName, mimeType) => {
    const blob = new Blob([data], { type: mimeType });
    const url = window.URL.createObjectURL(blob);
    downloadURL(url, fileName, mimeType);
    setTimeout(() => {
      window.URL.revokeObjectURL(url);
    }, 1000);
  };

  const downloadURL = (data, fileName) => {
    const a = document.createElement("a");
    a.href = data;
    a.download = fileName;
    document.body.appendChild(a);
    a.style = "display: none";
    a.click();
    a.remove();
  };

  export default {
    name: "docx2",
    data() {
      return {
        data: JSON.stringify(json, null, 4)
      };
    },
    methods: {
      generateDoc() {
        var docx = require("docx");
        var doc = new docx.Document();

        var paragraph = new docx.Paragraph("Some cool text here.");
        paragraph.addRun(new docx.TextRun("Lorem Ipsum Foo Bar"));
        doc.addParagraph(paragraph);

        var packer = new docx.Packer();
        packer.toBuffer(doc).then((buffer) => {
          // fs.writeFileSync("My First Document.docx", buffer);
          saveDataToFile(
            buffer,
            ".docx",
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
          );
        });
      }
    }
  };
</script>

<style scoped lang="scss">
    textarea {
        width: 600px;
        height: 600px;
    }

    p {
        color: green;
    }
</style>
